.navBarLogo {
    width: 18vw;
    pointer-events: none;
}
.navBarBlock{
    background-color: #FFFFFF;
    height: 8vh;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 2vw;
    border-bottom-right-radius: 15px;
    border-bottom-left-radius: 15px;
}
.accountBtn{
    background-color: rgba(255, 255, 255, 0);
    border: none;
    width: 24px;
    height: 24px;
    margin: 0 1vw;
    opacity: 0.55;
    background-image: url('../../img/Frame.svg');
    background-repeat: no-repeat;
}
.accountText{
    display: flex;
    flex-direction: row;
    font-size: 14px;
    font-weight: bold;
    margin-right: 5px;
    opacity: 0.7;
}
.burgerBtn{
    background-color: rgba(255, 255, 255, 0);
    border: none;
    width: 24px;
    height: 24px;
    background-image: url('../../img/Frame2.svg');
    background-repeat: no-repeat;
}
.logInBlock{
    display: flex;
    border: 0;
    font-size: 14px;
    background-color: rgba(255, 255, 255, 0);
    align-items: center;
    flex-direction: row;
    text-decoration: none;
    color: #5B5B5B;
}
.logOutBlock{
    display: flex;
    border: 0;
    font-size: 14px;
    padding: 0;
    background-color: rgba(255, 255, 255, 0);
    align-items: center;
    flex-direction: row;
    text-decoration: none;
    color: #5B5B5B;
}
